let page = 1
export default {
  data () {
    return {
      list: [],
      loadingType: 0
    }
  },

  onLoad () {
    this.$http.getaliveindex({
        isnewperson: 1
      }).then(res => {
      if (res.status === 200) {
        this.list = res.data.data
      } 
    })
  },

  methods: {
    linkTo (item) {
      if(!uni.getStorageSync('token')) {
        this.getnav(`/pages/loginSon/login`)

        return false
      }
      uni.setStorageSync('pullurl', item.addressitem)
      uni.setStorageSync('shopid', item.shop_id)
      this.getnav(`/pages/live/Audience`)
    },
  },

  onReachBottom () {
    if (this.loadingType === 2) return false
    this.loadingType = 1
    this.$http.getaliveindex({
      isnewperson: 1,
      page: page + 1
    }).then(res => {
      if (res.status === 200) {
        this.loadingType = 0
        if (!res.data.data.length) {
          this.loadingType = 2
        }
        if (res.data.data.length < 10) {
          this.loadingType = 2
          page = page + 1
        }
        if (res.data.data.length) {
          this.list = [...this.list, res.data.data]
          page = page + 1
        }
      } else {
        this.loadingType = 0
      }
    }).catch(() => {
      this.loadingType = 0
    })
  }
}
